<template>
  <div id="content">
    <div id="example">
    </div>
    <div class="leftSlider">
      <!--3<span @click="isShow"> 11111111<br><img src="../assets/image/test.jpg" style="width: 30px;height: 30px"></span>-->
      <div @click="add"></div>
      <div @click="toggle">(HTML marker)</div>
      <div @click="removeAll"></div>
    </div>
  </div>
</template>

<script>
  // photo-sphere-viewer3.6.0   https://photo-sphere-viewer-3.netlify.app
  import PhotoSphereViewer from 'photo-sphere-viewer'
  import 'photo-sphere-viewer/dist/photo-sphere-viewer.css'
  import img from '@/assets/image/2.jpg'
  import markImg from '@/assets/image/1.png'
  export default {
    data () {
      return {
        PSV: null,
        markImg: markImg,
        marker: {
          x: 2000,
          y: 1000
        }
      }
    },
    methods: {
      test () {
        this.PSV =  PhotoSphereViewer({
          container: document.getElementById('example'),
          panorama: img,
          size: {
            width: '100%',
            height: '100%'
          },
          caption: '',
          anim_speed: '1rpm',
          loading_txt: '...',
          time_anim: false,
          markers: [
            {
              id: 'text',
              x: 2001,
              y: 1077,
              image: this.markImg,
              width: 32,
              height: 32,
            },
            {
              id: '1',
              image: this.markImg,
              width: 32,
              height: 32,
              x: 1790,
              y: 944,
              tooltip: '1'
            },
            {
              id: '2',
              image: this.markImg,
              width: 32,
              height: 32,
              x: 1656,
              y: 855,
              tooltip: '2'
            }
          ],
          navbar: [
            'autorotate',
            'zoom',
            'markers',
            {
              id: 'my-button',
              title: 'Hello world',
              className: 'custom-button',
              content: 'Custom',
              onClick: function() {
                alert('Hello from custom button');
              }
            },
            'caption',
            'fullscreen'
          ]
        })
        this.PSV.on('select-marker', function(marker) {
          console.log(marker)
        })
        this.PSV.on('select-marker-list',function (arr) {
          console.log(arr)
        })
      },
      add (e) {
        e.stopPropagation()
       if (this.marker.x < 3000) {
         this.marker.x += Math.random()*100
         this.marker.y += Math.random()*100
       }
        let s = {
            id: Math.random(),
            image: this.markImg,
            width: 32,
            height: 32,
            x: this.marker.x,
            y:this.marker.y,
            tooltip: '<div>3<br>2020-05-03<br>2020-09-01</div>'
          }
        this.PSV.addMarker(s, true)
      },
      removeAll (e) {
        e.stopPropagation()
        this.PSV.clearMarkers()
      },
      toggle () {
        this.PSV.toggleMarker('text')
      },
    },
    mounted:function () {
      this.test()
    }
  }
</script>

<style scoped>
  #content{
    width: 100vw;
    height: 100vh;
    position: relative;
  }
  #example{
    width: 100vw;
    height: 100vh;
  }
  .leftSlider{
    position: absolute;
    width:200px;
    height:200px;
    background: rgba(255,255,255,0.5);
    left: 20px;top:20px;
    cursor: pointer;
    /**/
    z-index: 999;
    padding: 20px;
  }
</style>



